import { useState, useEffect } from "react";
import GroupSelector from "../components/PlanifierSoutenance/GroupSelector";
import JurySelector from "../components/PlanifierSoutenance/JurySelector";
import RoomSelector from "../components/PlanifierSoutenance/RoomSelector";
import DateTimePicker from "../components/PlanifierSoutenance/DateTimePicker";
import SubmitButton from "../components/PlanifierSoutenance/SubmitButton";

interface Group {
  id: string;
  name: string;
}

interface Jury {
  idJury: number;
  nom: string;
  valideDeliberation: number; // TINYINT(1) as a number (0 or 1)
}

interface Room {
  id: string;
  name: string;
}

interface Soutenance {
  date: string;
  time: string;
  location: string;
  jury: string; // Jury is a string now (jury name)
  group: string;
}

const PlanifierSoutenance = () => {
  const [newSoutenance, setNewSoutenance] = useState<Soutenance>({
    date: "",
    time: "",
    location: "",
    jury: "",
    group: "",
  });

  const [groups, setGroups] = useState<Group[]>([]);
  const [jurys, setJurys] = useState<Jury[]>([]);
  
  // Static room data
  const rooms: Room[] = [
    { id: "1", name: "Room A" },
    { id: "2", name: "Room B" },
    { id: "3", name: "Room C" },
  ];

  const [loading, setLoading] = useState<boolean>(true);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const [, jurysResponse] = await Promise.all([
          fetch("/api/groups"),
          fetch("/api/jurys"),
        ]);

        const jurysData = await jurysResponse.json();
        setJurys(jurysData);

        console.log("Jurys Data:", jurysData);

      } catch (error) {
        console.error("Error fetching data:", error);
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, []);

  const addSoutenance = () => {
    if (
      newSoutenance.date &&
      newSoutenance.time &&
      newSoutenance.location &&
      newSoutenance.jury &&
      newSoutenance.group
    ) {
      alert("Soutenance ajoutée avec succès !");
      console.log("Nouvelle soutenance:", newSoutenance);
      setNewSoutenance({ date: "", time: "", location: "", jury: "", group: "" });
    } else {
      alert("Veuillez remplir tous les champs !");
    }
  };

  if (loading) {
    return <div className="p-8 text-center">Loading...</div>;
  }

  return (
    <div className="p-8 bg-gray-50 min-h-screen">
      <h1 className="text-4xl font-bold text-gray-800 mb-8 text-center">📅 Planification des Soutenances</h1>

      <div className="max-w-4xl mx-auto bg-white p-6 rounded-lg shadow-xl mb-8">
        <h2 className="text-2xl font-semibold text-gray-700 mb-6">📝 Définir une nouvelle soutenance</h2>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          <GroupSelector
            groups={groups}
            selectedGroup={newSoutenance.group}
            onSelectGroup={(group) => setNewSoutenance({ ...newSoutenance, group })}
          />
          <DateTimePicker
            date={newSoutenance.date}
            time={newSoutenance.time}
            onDateChange={(date) => setNewSoutenance({ ...newSoutenance, date })}
            onTimeChange={(time) => setNewSoutenance({ ...newSoutenance, time })}
          />
          <RoomSelector
            rooms={rooms}
            selectedRoom={newSoutenance.location}
            onSelectRoom={(location) => setNewSoutenance({ ...newSoutenance, location })}
          />
          <JurySelector
            selectedJury={newSoutenance.jury}
            onSelectJury={(jury) => setNewSoutenance({ ...newSoutenance, jury })}
          />
        </div>
        <SubmitButton onClick={addSoutenance} />
      </div>
    </div>
  );
};

export default PlanifierSoutenance;
