import { FaCalendarAlt, FaClock } from "react-icons/fa";

interface DateTimePickerProps {
  date: string;
  time: string;
  onDateChange: (date: string) => void;
  onTimeChange: (time: string) => void;
}

const DateTimePicker = ({ date, time, onDateChange, onTimeChange }: DateTimePickerProps) => {
  return (
    <>
      <div className="flex items-center gap-3 bg-gray-50 p-4 rounded-lg shadow-sm">
        <FaCalendarAlt className="text-gray-600" />
        <input
          type="date"
          value={date}
          onChange={(e) => onDateChange(e.target.value)}
          className="p-2 border rounded w-full focus:ring-2 focus:ring-blue-500"
        />
      </div>
      <div className="flex items-center gap-3 bg-gray-50 p-4 rounded-lg shadow-sm">
        <FaClock className="text-gray-600" />
        <input
          type="time"
          value={time}
          onChange={(e) => onTimeChange(e.target.value)}
          className="p-2 border rounded w-full focus:ring-2 focus:ring-blue-500"
        />
      </div>
    </>
  );
};

export default DateTimePicker;